//import { useStages } from "../../Store"
import { PageContainer, PageTitle } from "../../components"
import { DashboardItemType } from "../../types"


import recepcio from "../../assets/images/recepcio.png"
import nagyEloado from "../../assets/images/nagyeloado.png"
import nagyEloadonyitva from "../../assets/images/nagyeloadonyitva.png"
import szekcio1 from "../../assets/images/szekcio1.png"
import szekcio1nyitva from "../../assets/images/szekcio1nyitva.png"
import szekcio2 from "../../assets/images/szekcio2.png"
import szekcio2nyitva from "../../assets/images/szekcio2nyitva.png"
import szekcio3 from "../../assets/images/szekcio3.png"
import szekcio4 from "../../assets/images/szekcio4.png"
import iokCafe from "../../assets/images/iokcafe.png"
import messageBoard from "../../assets/images/messageboard.png"
import galeria from "../../assets/images/galeria.png"

import Dashboard from "../../components/Dashboard"
import Welcome from "../Welcome";
import { useMemo } from "react"
import { useLiveStaticElements } from "../../Store"




const Home = () => {

    const { galleryUrl } = useLiveStaticElements()

    const homeDashboardItems : DashboardItemType[] = useMemo(() => [

        {
            caption: "Nagyelad",
            title: "Dleltti plenris eladsok a nagyeladban",
            img: nagyEloado,
            hoverImg: nagyEloadonyitva,
            corner: "br",
            light: false,
            link: "/szekcio/plenaris",
            mobileOrder: 1
        },
        {
            caption: "1. szekciterem",
            title: "Szakkpzsi, ITMP s NetAcad szekci eladsai a 1. szekciteremben",
            img: szekcio1,
            hoverImg: szekcio1nyitva,
            corner: "none",
            light: false,
            link: "/szekcio/szakkepzes-itmp-netacad",
            mobileOrder: 2
        },
        {
            caption: "2. szekciterem",
            title: "Digitlis kultra szekci eladsai a 2. szekciteremben",
            img: szekcio2,
            hoverImg: szekcio2nyitva,
            corner: "none",
            light: false,
            link: "/szekcio/digitalis-kultura",
            mobileOrder: 3
        },
        /* {
            caption: "IOK Cafe",
            title: "Rsztvevk egyms kztti l beszlgetse ngy tematikus asztalnl az IOK kvzjban",
            img: iokCafe,
            corner: "bl",
            light: true,
            link: "/iok-cafe",
            mobileOrder: 6
        }, */
        {
            caption: "Galria",
            title: "Megnzheted az esemnyen kszlt ftkat",
            img: galeria,
            corner: "bl",
            light: true,
            link: galleryUrl,
            external: true,
            mobileOrder: 6
        },
        {
            caption: "Informcis pult",
            title: "Ksznt, programfzet, eladi tabl, visszajelz krdiv, tmogati szrlap, rtkel rlap, HTTP-csapat brossra",
            img: recepcio,
            corner: "tr",
            light: true,
            link: "/infopult",
            mobileOrder: 0
        },
        {
            caption: "3. szekciterem",
            title: "IT felsoktats szekci eladsai a 3.szekciteremben",
            img: szekcio3,
            hoverImg: szekcio1nyitva,
            corner: "none",
            light: false,
            link: "/szekcio/it-felsooktatas",
            mobileOrder: 4
        },
        {
            caption: "4. szekciterem",
            title: "Digitlis kultra az alstagozatban eladsok a 4. szekciteremben",
            img: szekcio4,
            hoverImg: szekcio2nyitva,
            corner: "none",
            light: false,
            link: "/szekcio/digitalis-kultura-also-tagozat",
            mobileOrder: 5
        },
        {
            caption: "zenfal",
            title: "zenfal fontos zenetekkel s felhvsokkal a szervezktl",
            img: messageBoard,
            corner: "tl",
            light: true,
            link: "/uzenofal",
            mobileOrder: 7
        },
            
    ], [galleryUrl])
    
	
    return (

            <PageContainer container>
                <PageTitle>IOK 2022 Aula</PageTitle>
                <Dashboard items={homeDashboardItems} />
            </PageContainer>
        
    )
}

export default Home




